* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: #ffffff;
}

a:hover {
  color: #000000;
}

video {
  max-width: 450px;
}

img {
  width: 100%;
}

ul {
  list-style-type: none;
}

input {
  font-size: 12px;
  border: none;
  background-color: inherit;
  padding: 0 5px;
}
input:hover {
  opacity: 0.75;
  cursor: pointer;
}

body {
  font-family: Arial;
  color: #ffffff;
  width: 100%;
  text-align: justify;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 1px;
  background-color: #8d0505;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Merriweather", serif;
  line-height: 22px;
  letter-spacing: 3px;
  text-align: center;
}

hr {
  opacity: 0.3;
}

select {
  border: none;
  background-color: inherit;
}

.margin_10 {
  margin: 0 15%;
}

.place-holder {
  height: 75px;
}

.grid-1 {
  grid-row: 1;
}

.navigator {
  position: fixed;
  width: 100%;
  letter-spacing: 4px;
}
.navigator__container {
  display: grid;
  grid-template-columns: 60% auto;
  background-color: #fc0505;
  padding: 5px 30px;
}
.navigator__logo {
  padding-left: 20px;
}
.navigator__logo:first-child {
  max-width: 150px;
}
.navigator__box {
  display: grid;
  grid-template-columns: auto auto auto auto;
  font-size: 16px;
  align-items: center;
}
.navigator__box a {
  padding: 5px;
  border-radius: 10px;
}
.navigator__box a:hover {
  background-color: #eff301;
}

.main {
  padding-bottom: 30px;
}
.main__box {
  margin-top: 70px;
  display: grid;
  grid-template-columns: 50% auto;
  grid-gap: 20px;
}
.main h4,
.main p {
  padding: 10px 0;
}
.main__form {
  width: 100%;
  font-size: 14px;
  padding: 5px;
  margin: 3px 0;
  border: 1px solid #000000;
  background-color: #ffffff;
}
.main__hero {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.main button {
  width: 50px;
}

.footer {
  padding: 20px;
  text-align: center;
  background-color: #fc0505;
}